@import '../../../../../../commun/styles/sass/mixins';

.usersContainer {
  width: 100%;
  height: 100%;
  border: 1px black solid;
  @include flex(row, left, flex-start, wrap);

  .pageNumberContainer {

    padding: 2px;
    margin: 3px;
    border: 1px solid green ;
    width: 100%;
    @include flex(row, space-around, center, wrap);


    .selectedPage {
      font-weight: bold;
      color: black;
  }
    .nonSelectedPage {
      color: dimgrey;
      margin: 3px;
    }
}


}
