@import '../../../../../commun/styles/sass/mixins';


.dialogsItemsContainer {
  @include flex(column, center, center, wrap);
  background-color: saddlebrown;
  margin: 10px 10px 10px 10px;



  .dialog a{
    @include flex(column, center, center, wrap);
    border: 1px solid brown;
    width: 100%;
    height: 100%;
    color: wheat;
    text-decoration: none;



  }
  .dialog a.active {
    color: gold;
  }
}